* {
  margin: 0;
  padding: 0;
  
}

body {
  background-image: url(./images/bg-intro-desktop.png);
  background-color: hsl(0, 100%, 74%);
}
.App {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  margin: 0px 140px;
  
}

.content {
  color: #fff;
  box-sizing: border-box;
  padding: 40px;
  width: 50%;
}


.content h1 {
  font-size: 50px;
  margin-bottom: 30px;
  line-height: 55px;
  
}

.box {
  padding: 20px;
  width: 50%;
}

.small_box {
  color: white;
  background-color: hsl(248, 32%, 49%);
  width: 100;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  background-image:none;
  
}

.small_box p,
.small_box strong {
  background: none;
  
}

.signup-box {
  background-color: #fff;
  margin-top: 20px;
  border-radius: 10px;
  border: none;
}

.signup-box-items {
  padding: 30px;
  background-color: #fff;
  border-radius: 10px;
  background-image: none;

}

input {
  width: 100%;
  background:none;
  border: 1px solid grey;
  box-sizing: border-box;
  padding: 10px;
  margin-bottom: 20px;
  border-radius: 3px;
}

::placeholder{
  font-weight: 600;
}

button {
  width: 100%;
  background:none;
  border: none;
  box-sizing: border-box;
  padding: 15px;
  margin-bottom: 10px;
  border-radius: 3px;
  background-color: hsl(154, 59%, 51%);
  color: white;
  font-weight: bold;
  letter-spacing: 2px;
  cursor: pointer;
  transition: transform 0.4s ease;
}

button:hover {
  transform:translateY(-4px)
}

.text {
  text-align: center;
  font-size: 12px;
}

.text p {
  color: grey;
}

.red-text {
  color: hsl(0, 100%, 74%);
  font-weight: bold;
  cursor: pointer;
}

.alert-text {
  color: hsl(0, 100%, 74%);
  text-align: right;
  font-size: 12px;
}


@media screen and (max-width:400px) {
  .App {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 10px;
  }
  .content {
    padding: 0;
  }
  .content h1,
  .content p {
    text-align: center;
  }

  .content h1 {
    font-size: 30px;
    line-height: 40px;
  }

  .small_box {
    text-align: center;
    padding: 15px;
  }
}